import axios from 'axios';
import { ActionContext } from 'vuex';
import { Categories } from '@/entities/categories/model/categories.types';
import { API_BASE_URL } from '@/shared/api'

interface CategoriesState {
    categoriesData: Categories[];
}

export const categoriesModule = {
    namespaced: true,
    state: (): CategoriesState => ({
        categoriesData: []
    }),
    mutations: {
        setCategories(state: CategoriesState, categories: Categories[]) {
            state.categoriesData = categories;
        }
    },
    actions: {
        async fetchCategories({ commit }: ActionContext<CategoriesState, any>) {
            try {
                const response = await axios.get(`${API_BASE_URL}categories`);
                const categories: Categories[] = response.data;
                commit('setCategories', categories);
            } catch (error) {
                console.error('Ошибка при получении данных категорий:', error);
            }
        }
    },
};