import axios from 'axios';
import { Reviews } from '@/entities/reviews/model/reviews.types'
import { API_BASE_URL } from '@/shared/api'

interface ReviewsState {
    ReviewsData: Reviews[];
    expandedReviews: Record<string, boolean>; 
}


export const reviewsModule = {
    namespaced: true,
    state: (): ReviewsState => ({
        ReviewsData: [],
        expandedReviews: {} 
    }),
    
    mutations: {
        setReviews(state: ReviewsState, reviews: Reviews[]) {
            state.ReviewsData = reviews;
        },
        toggleShowFull(state: ReviewsState, id: string) {
            state.expandedReviews[id] = !state.expandedReviews[id];
        }
    },
    
    actions: {
        async fetchReviews({ commit }: { commit: Function }) {
            try {
                const response = await axios.get<Reviews[]>(`${API_BASE_URL}reviews`);
                const reviews: Reviews[] = response.data;
                const filteredReviews = reviews.filter(review => review.stars >= 4);
                commit('setReviews', filteredReviews);
            } catch (error) {
                console.error('Ошибка при получении данных результатов:', error);
            }
        }
    },    
    getters: {
        reviewsWithShowFlag: (state: ReviewsState): (Reviews & { showFull: boolean })[] => {
            return [...state.ReviewsData]
                .sort((a, b) => new Date(b.date).getTime() - new Date(a.date).getTime())
                .map(review => ({
                    ...review,
                    showFull: state.expandedReviews[review.id] || false
                }));
        }
    }
    
}