<script setup lang="ts">
import { Container } from '@/shared/ui/container';
import { Paragraph } from '@/shared/ui/text/paragraph';
import { Button } from '@/shared/ui/button';
import { ref, onMounted } from 'vue';
import axios from 'axios';
import { API_BASE_URL } from '@/shared/api';

const banners = ref<{ id: string; photo: string; title: string; link: string, text: string }[]>([]);

onMounted(async () => {
  try {
    const response = await axios.get(`${API_BASE_URL}banners`);
    banners.value = response.data;
  } catch (error) {
    console.error('Ошибка при получении баннеров:', error);
  }
});
</script>

<template>
  <section class="section-first">
    <div class="section-first__content">
      <RouterLink class="banner" v-for="banner in banners" :key="banner.id" :to="banner.link">
        <Paragraph tagName="p" color="white" size="xxxl" class="title">
          {{ banner.title }}
        </Paragraph>
        <Paragraph tagName="p" color="white" size="xxxl" class="description">
          {{ banner.text }}
        </Paragraph>
      </RouterLink>
    </div>
  </section>
</template>

<style lang="scss" scoped>
@import './style.scss';
</style>
