<script setup lang="ts">
import { Container } from '@/shared/ui/container';
import { Heading } from '@/shared/ui/text/heading';
import { SliderCards } from '@/features/slider-cards'
import { RouterLink } from 'vue-router';
import { useSwipe } from '@/shared/composables/useTouchSwipe';
import { ref, onMounted } from 'vue';
import axios from 'axios';
import { API_BASE_URL } from '@/shared/api';

interface Category {
    id: string;
    name: string;
    slug: string;
    full_slug: string;
}

interface Product {
  id: string;
  name: string;
  slug: string;
  image: string;
  price: string;
  promotional_price: string;
  categories: Category[];
}


const itemsContainer = ref<HTMLElement | null>(null);
const contentCards = ref<HTMLElement | null>(null);
useSwipe({
    elementRef: itemsContainer,
    transformElementRef: contentCards,
});

const products = ref<Product[]>([]);

onMounted(async () => {
    try {
        const response = await axios.get(`${API_BASE_URL}products`);
        products.value = response.data.filter((p: Product) =>
            p.name.toLowerCase().includes('букет')
        );
    } catch (error) {
        console.error('Ошибка при получении товаров:', error);
    }
});
</script>

<template>
    <section class="section-bukety">
        <Container>
            <RouterLink to="/buket-cvetov/">
                <Heading tagName="h2" regular size="xl">Букеты</Heading>
            </RouterLink>

            <div class="section-second__items" ref="itemsContainer">
                <div class="content__slider" ref="contentCards">
                    <SliderCards v-for="product in products" :key="product.id" :cardData="product" />
                </div>
            </div>
        </Container>
    </section>
</template>


<style lang="scss" scoped>
@import './style.scss';

.content__slider {
    display: flex;
    gap: 20px;
    transition: transform 0.3s ease;
    width: 100%;
    overflow: hidden;
}

</style>
