import axios from 'axios';
import { Product} from './product.types';
import { API_BASE_URL } from '@/shared/api';

interface ProductsState {
    productsData: Product[];
    searchResults: Product[];
    
    currentPage: number; 
    casesPerPage: number; 
}

export const productsModule = {
    namespaced: true,
    state: (): ProductsState => ({
        productsData: [],
        searchResults: [],
        currentPage: 1,
        casesPerPage: 10,
    }),
    mutations: {
        setProducts(state: ProductsState, cases: Product[]) {
            state.productsData = cases;
        },
        setSearchResults(state: ProductsState, results: Product[]) {
            state.searchResults = results;
        },
    },
    actions: {
        async fetchProducts({ commit }: { commit: Function }) {
            try {
                const response = await axios.get<Product[]>(`${API_BASE_URL}products`);
                const cases: Product[] = response.data;
                commit('setProducts', cases);
            } catch (error) {
                console.error('Ошибка при получении данных:', error);
            }
        },
        searchProducts({ commit, state }: { commit: Function; state: ProductsState }, query: string) {
            const results = state.productsData.filter((product) => {
                const productName = `${product.name}`;
                
                return (
                    productName.toLowerCase().includes(query.toLowerCase())
                );
            });
            commit('setSearchResults', results);  
        },
    },
    getters: {
        getProductById: (state: ProductsState) => (id: string) => {
            return state.productsData.find((Product: Product) => Product.id === id) || null;
        },
        getProductBySlug: (state: ProductsState) => (slug: string) => {
            return state.productsData.find((Product: Product) => Product.slug === slug) || null;
        },
        searchResults(state: ProductsState) {
            return state.searchResults;
        },
    },
};
